import React, { Component, useContext } from "react";
import {
  Search,
  Person,
  Chat,
  Notifications,
  Settings,
} from "@material-ui/icons";
import "./topbar.css";
import { Link } from "react-router-dom";
import { AuthContext } from "../../context/AuthContext";

export default function Topbar() {
  const { user } = useContext(AuthContext);
  const PF = process.env.REACT_APP_PUBLIC_FOLDER;

  const logoutClicked = () => {
    console.log("logout clicked");
    localStorage.removeItem("user");
    window.location.replace("/");
  };
  return (
    <div className="topbarContainer">
      <div className="topbarLeft">
        <Link to="/" style={{ textDecoration: "none" }}>
          <span className="logo">Motor Media</span>
        </Link>
        <button className="logoutButton" onClick={logoutClicked}>
          log out
        </button>
      </div>
      <div className="topbarCenter">
        <div className="searchbar">
          <Search className="searchIcon" />
          <input
            placeholder="Search for friend loser"
            className="searchInput"
          />
        </div>
      </div>
      <div className="topbarRight">
        <div className="topbarLinks">
          <span className="topbarLink">homepage</span>
          <span className="topbarLink">time line</span>
        </div>
        <div className="topbarIcons">
          <div className="topbarIconItem">
            <Person />
            <span className="topbarIconBadge">1</span>
          </div>
          <div className="topbarIconItem">
            <Chat />
            <span className="topbarIconBadge">2</span>
          </div>
          <div className="topbarIconItem">
            <Notifications />
            <span className="topbarIconBadge">3</span>
          </div>
        </div>
        <Link to={`/setting/${user.username}`}>
          <Settings className="topbarSetting" />
        </Link>
        <Link to={`/profile/${user.username}`}>
          <img
            src={
              user.profilePicture
                ? PF + "person/" + user.profilePicture
                : PF + "person/" + "andrew.jpg"
            }
            className="topbarImg"
          />
        </Link>
      </div>
    </div>
  );
}
