import { useRef, useContext } from "react";
import "./login.css";
import { loginCall } from "../../apiCalls";
import { AuthContext } from "../../context/AuthContext";
import { Link } from "react-router-dom";
import { CircularProgress } from "@material-ui/core";

export default function Login() {
  const email = useRef();
  const password = useRef();
  const { user, isFetching, error, dispatch } = useContext(AuthContext);

  const handleClick = (e) => {
    console.log(email.current.value);
    e.preventDefault();
    loginCall(
      { email: email.current.value, password: password.current.value },
      dispatch
    );
  };

  console.log(user);
  return (
    <div className="login">
      <div className="loginWrapper">
        <div className="loginLeft">
          <h3 className="loginLogo">MotorMedia</h3>
          <span className="loginDesc">
            Welcome. this is a place where you meet cool people. If you are not
            one, then Fuck off.
          </span>
        </div>
        <div className="loginRight">
          <form className="loginBox" onSubmit={handleClick}>
            <input
              placeholder="Email"
              ref={email}
              type="email"
              className="loginInput"
              required
            />
            <input
              placeholder="Password"
              type="password"
              className="loginInput"
              minLength="6"
              required
              ref={password}
            />
            <button type="submit" disabled={isFetching} className="loginButton">
              {isFetching ? <CircularProgress size="15px" /> : "Log in"}
            </button>
            <span className="loginForgot">Forgot Passowrd?</span>
            <Link to="/register">
              <button className="loginRegisterButton">
                create a New Account
              </button>
            </Link>
          </form>
        </div>
      </div>
    </div>
  );
}
